@import '../constants/colors';

#email-list {
  padding: 1rem;
  .email-preview {
    display: inline-flex;
    overflow: hidden;
    width: 100%;
    word-break: keep-all;
    white-space: nowrap;
    background-color: $lighter-grey;
    border: $grey 1px solid;
    padding: 0.5rem;

    &:hover {
      box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
      position: relative;
      z-index: 10;
    }

    span {
      margin-right: 1rem;
    }

    .sender {
      width: 16em;
      font-weight: bold;
    }

    .subject {
      font-weight: bold;
    }

    .body {
      text-overflow: ellipsis;
      overflow: hidden;
      color: $dark-grey;
    }

    .date {
      margin-left: auto;
      font-weight: bold;
    }
  }
}
